import React , {useEffect, useState} from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY, gen16CodeId, ObjectKeys, parseFloatToDecimal, updStore } from "../../../stores/Utils";
import { get_filter_by_acc,  calcSubmayor, get_filter_paystub } from "../services/server_fetching";
import { useNavigate } from "react-router";
import { CloseModal, OpenModal } from "../../../components/DialogHRM";
import { SvgIcon_Cmpt, CalendarModalPicker_Cmpt } from "../../../components";



const CalendarModalPicker = CalendarModalPicker_Cmpt();
const Svg = SvgIcon_Cmpt();

const ViewComprobante  = ({ data, item }) => {
	const [obs, set_obs] = useState(0);


	
	useEffect(() => {
		//get_filter_by_acc(item,updObs);
	},[item]);

	const updObs =  () => {
		set_obs(gen16CodeId())
	}



	const navigate = useNavigate();

	const close =  (element) => {
		CloseModal({id:data.modalID});
		navigate({
			pathname: "accounting-ledger" ,
			search: `?cId=${element.comprobanteId}`
		});
	}
	


	const confirmCalendar = (v) => {
		if(v?.initDate && v?.lastDate ){
			let nv = {...v};
			nv.initDate = nv.initDate -100;
			nv.lastDate = nv.lastDate + 86380000;
			updStore("date_range_comps",nv);
		}
		get_filter_paystub( item?.employeeId, updObs)
	}
	

    const openCalendar = () => {
        let modal_data = {};
        modal_data["zIndex"] = 950;
        modal_data["height"] = "80vh";
        // data['observeResize']=true;
        modal_data["props"] = { minHeight: "1vh", overlay: true };
        modal_data["content"] = 
			<CalendarModalPicker 
				onConfirm={confirmCalendar} 
				init={getStore("date_range_comps")?.initDate}
				last={getStore("date_range_comps")?.lastDate}
				isRange={true}
			/>
        
        OpenModal(modal_data);
	}
	

	let dateRangeString = getStore("date_range_comps")?.initDate && parseDateMMDDYYYY(getStore("date_range_comps")?.initDate)  + "-" +parseDateMMDDYYYY(getStore("date_range_comps")?.lastDate);



	return (
		<div style={{
			maxHeight: "80vh",
			width: "960px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Movimientos de Nomina"}
				</p>
				<div className="flexSpace"/>
				<div>
					<div className={"filter_date_btn _dsplFlx"} onClick={openCalendar}>
						<div className="lbl date">
								{dateRangeString || "Fecha"}
						</div>
						<Svg name={"calendar"} color={"#66666"}/>
					</div>
				</div>
			</div>

			<div className=" acc_dtls" style={{marginTop: 10}}>
				<div className="_dsplFlx"  >
					<div className="code" style={{color: "#646cff"}}>
						{item?.name} 
					</div>
					<div className="name">
						{item?. unit}
					</div>
				</div>
			</div>
			
			
			<div className="_dsplFlx acc_dtls">
				
				<div className="flexSpace"/>
				<div className="_dsplFlx"  >
						<div className="code" style={{color: "var(--hrm_palette-item_coll_gray)"}}>
							{"Acumulado:"} 
						</div>
						<div className="name" style={{color: "var(--hrm_palette-inverse-background-b)"}}>
							${SumBalance(getStore("paystub_submayor_group"))?.toFixed(2)} 
						</div>
					</div>
			</div>




			
			<div className="_dsplFlx hdr_bar purple">
				<div className="tr_bar_30">
					Comprobante
				</div>
				<div className="tr_bar_20">
					Fecha
				</div>
				<div className="tr_bar_30">
					Documento
				</div>
				<div className="tr_bar_20">
					Amount
				</div>
			</div>

			<div className="scollVh subMHeight">
			{getStore("paystub_submayor_group") && ObjectKeys(getStore("paystub_submayor_group"))?.map((elId,ind43) => {

				let element = getStore("paystub_submayor_group")[elId];
				
				return(
					<div className="_dsplFlx hdr_bar" key={ind43}>
						<div className="tr_bar_30 comprobante_id" onClick={()=>close(element)}>
								{element.comprobanteId}
						</div>
						<div className="tr_bar_20">
							{element.date && parseDateMMDDYYYY(element.date)}
						</div>
						<div className="tr_bar_30">
							{element.document}
						</div>
						<div className="tr_bar_20">
							${(element.balance*1)?.toFixed(2)}
						</div>
					</div>
				)
			})}
			</div>
		</div>
	);
};

export default ViewComprobante;


const SumBalance = (obj) => {
	let balance = 0;
	obj && ObjectKeys(obj)?.map((flds, indE)=>{
		let amount = (obj[flds].balance*1);
		balance =  parseFloatToDecimal(balance,2)*1 + parseFloatToDecimal(amount,2)*1;
	});
	return  balance;
}  
